/**
 * 
 */
interface TransformOptions {
    locOrigion: Vector2;
    mapOrigin: Vector2;
    locRange: Vector2;
    mapRange: Vector2;
    mapAxisX: Vector2;
    mapAxisY: Vector2;
}

export class CoordTransformer {
    public static getVectorLen(v: Vector23) {
        if (v.z == null) {
            v.z = 0;
        }

        return Math.sqrt(v.x * v.x + v.y * v.y + v.z * v.z);
    }

    public margin?: TPosition; // 
    public locOrigion: Vector2 = { x: 0, y: 0 };
    public mapRange: Vector2 = { x: 0, y: 0 }; // 

    // tslint:disable-next-line: variable-name
    private _locRange?: Vector2;
    private mapAxisX: Vector2 = { x: 0, y: 0 }; // x
    private mapAxisY: Vector2 = { x: 0, y: 0 }; // y

    private isInit: boolean = true; // 

    public set locRange(data: Vector2) {
        if (!this._locRange) {
            this._locRange = data;
        } else {
            console.error('');
        }
    }

    public parseCood(data: Vector23 | Vector23[]) {
        if (this._locRange && this.margin) {
            if (Array.isArray(data)) {
                return data.map(this.transform.bind(this));
            }

            return this.transform(data);
        }

        console.error('');
    }

    /**
     * 
     */
    public parseTransform(mapParas: TPosition | undefined = this.margin) {
        if (!mapParas || mapParas.length < 4) {
            console.error('4');
        } else {
            const mapAxisX = { x: mapParas[3].x - mapParas[0].x, y: mapParas[3].y - mapParas[0].y };
            const mapAxisY = { x: mapParas[1].x - mapParas[0].x, y: mapParas[1].y - mapParas[0].y };
            const mapRange = {
                x: CoordTransformer.getVectorLen(mapAxisX),
                y: CoordTransformer.getVectorLen(mapAxisY)
            };

            // 
            mapAxisX.x /= mapRange.x;
            mapAxisX.y /= mapRange.x;
            mapAxisY.x /= mapRange.y;
            mapAxisY.y /= mapRange.y;

            return {
                mapAxisX,
                mapAxisY,
                mapRange
            };
        }
    }


    /**
     * 
     */
    public transform(loc: Vector23): Vector3 {
        if (!this.valid()) {
            return { x: 0, y: 0, z: 0 };
        }

        if (this.isInit) {
            const params = this.parseTransform();

            if (params) {
                Object.assign(this, params);
                this.isInit = false;
            }
        }

        return this._transform(loc, {
            mapOrigin: this.margin![0],
            mapAxisX: this.mapAxisX,
            mapAxisY: this.mapAxisY,
            mapRange: this.mapRange,
            locRange: <Vector2>this._locRange,
            locOrigion: this.locOrigion
        });
    }

    /**
     * 
     */
    public getCoordinate(v: Vector23): Vector3 {
        if (!this.valid()) {
            return { x: 0, y: 0, z: 0 };
        }

        // tslint:disable-next-line:variable-name
        let _this: any = this;
        if (this.isInit) {
            _this = this.parseTransform();
        }

        const locRange = <Vector2>this._locRange;
        const tmp = this.parseTransform([
            { x: 0, y: 0 },
            { x: 0, y: locRange.y },
            { x: locRange.x, y: locRange.y },
            { x: locRange.x, y: 0 }
        ]);

        return this._transform(v, {
            mapOrigin: { x: 0, y: 0 },
            locRange: _this.mapRange,
            locOrigion: this.margin![0],
            mapAxisX: tmp!.mapAxisX,
            mapAxisY: tmp!.mapAxisY,
            mapRange: tmp!.mapRange,
        });
    }

    /**
     * 
     * @param options.locOrigion  
     * @param options.mapOrigin 
     * @param options.mapAxisX  X
     * @param options.mapAxisY  Y
     * @param options.locRange 
     * @param options.mapRange 
     */
    private _transform(loc: Vector23, options: TransformOptions) {
        const vector: any = { ...loc };
        vector.x = vector.x || vector.xaxis || 0;
        vector.y = vector.y || vector.yaxis || 0;
        vector.z = vector.z || vector.zaxis || null;

        const offsetX = (vector.x - options.locOrigion.x) / options.locRange.x * options.mapRange.x;
        const offsetY = (vector.y - options.locOrigion.y) / options.locRange.y * options.mapRange.y;

        return {
            x: options.mapOrigin.x + options.mapAxisX.x * offsetX + options.mapAxisY.x * offsetY,
            y: options.mapOrigin.y + options.mapAxisX.y * offsetX + options.mapAxisY.y * offsetY,
            z: vector.z == null ? 1 : vector.z
        };
    }

    private valid() {
        if (!this.margin) {
            console.error('!');
            return false;
        }

        if (!this._locRange) {
            console.error('!');
            return false;
        }

        return true;
    }
}
